/* Core container styles */
.experience-container {
  padding: 100px 20px;
  text-align: center;
  background-color: black;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.experience-title {
  color: #5aff5a;
  font-size: 2rem;
  text-align: center;
  text-shadow: 0 0 10px #5aff5a;
}

.experience-subtitle {
  text-align: center;
  max-width: 700px;
  font-size: 1rem;
  margin: 1rem 0 2rem;
}

.experience-swiper {
  width: 100%;
  max-width: 600px; /* controls overall width */
  margin: 0 auto;
  padding: 20px 0;
}

.media-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.media-wrapper {
  width: 100%;
  max-height: 600px; /* limits vertical size */
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 16px;
  box-shadow: 0 0 16px #00ff88;
  background-color: black;
  overflow: hidden;
}

.slide-media,
.swiper-video {
  width: 100%;
  height: auto;
  max-height: 80vh;
  object-fit: contain;
  border-radius: 12px;
  box-shadow: 0 0 18px rgba(0, 255, 0, 0.3);
}

.swiper-video {
  max-height: 400px;
  max-width: 100%;
  width: auto;
  height: auto;
  display: block;
  object-fit: contain;
  margin: 0 auto;
  border-radius: 8px;
}

.slide-media {
  max-width: 100%;
  max-height: 400px;
  width: auto;
  height: auto;
  object-fit: contain;
  border-radius: 8px;
  display: block;
  margin: 0 auto;
}

.swiper-button-prev,
.swiper-button-next {
  color: #59ff71 !important; /* neon green */
  text-shadow: 0 0 10px #59ff71;
}


.slide-caption {
  margin-top: 12px;
  font-size: 1rem;
  margin-top: 10px;
  font-weight: bold;
  color: #90ee90;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 6px 18px;
  border-radius: 8px;
}

.experience-card {
  background-color: #111;
  border: 2px solid #5aff5a;
  border-radius: 12px;
  padding: 25px;
  margin: 25px auto;
  max-width: 800px;
  color: #e6e6e6;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  box-shadow: 0 0 30px rgba(0, 255, 0, 0.15);
  text-align: left;
}

.experience-card h3,
.experience-card h2 {
  color: #5aff5a;
  font-size: 1.6rem;
  margin-bottom: 12px;
}

.experience-card p {
  font-size: 1.05rem;
  margin-bottom: 8px;
}

.experience-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.experience-button {
  background-color: black;
  color: #00ff00;
  border: 2px solid #00ff00;
  padding: 10px 20px;
  border-radius: 6px;
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  text-decoration: none;
  transition: background 0.3s ease;
}

.experience-button:hover {
  background-color: #00ff00;
  color: black;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(10, 10, 10, 0.9);
  backdrop-filter: blur(4px);
  z-index: 9999;
  display: flex;
  overflow-y: auto ;
  align-items: center;
  justify-content: center;
}

.modal-content {
  position: relative;
  width: 90%;
  max-width: 900px;
  background: #111;
  border: 2px solid #00ff00;
  padding: 20px;
  border-radius: 10px;
  max-height: 90vh;
  overflow-y: auto;
}

.close-modal,
.close-button {
  position: absolute;
  top: 10px;
  right: 12px;
  font-size: 1.2rem;
  background: none;
  color: white;
  border: none;
  cursor: pointer;
}

@media (max-width: 768px) {
  .slide-media,
  .swiper-video {
    max-height: 300px;
    width: 100%;
  }

  .experience-buttons {
    flex-direction: column;
    align-items: center;
  }

  .experience-button {
    width: 90%;
    text-align: center;
  }

  .slide-caption {
    font-size: 0.7rem;
    padding: 5px 12px;
  }

  .modal-content {
    max-height: 90vh;
    overflow-y: auto;
  }
  .modal-content iframe,
  .modal-content embed {
    height: 90vh;
  }
}

@media (max-width: 480px) {
  .experience-title {
    font-size: 1.3rem;
  }

  .experience-card p {
    font-size: 0.95rem;
  }

  .swiper-video {
    max-height: 240px;
  }
}
